nav.navbar[_v-f47d2b8e] {
  height: 120vh;
  bottom: calc(-80vh + 70px);

  -webkit-transition: bottom .4s ease-in-out;

  transition: bottom .4s ease-in-out;

  border:none;
  background: -webkit-linear-gradient(top, rgba(255,255,255,0) 5vh, rgba(0,0,0,255) 45vh);
  background: linear-gradient(-180deg, rgba(255,255,255,0) 5vh, rgba(0,0,0,255) 45vh);
  /*background-color: gold;*/
  pointer-events: none;

}

nav.navbar.isOverlay[_v-f47d2b8e] {
  bottom: 0;
}

#navbar[_v-f47d2b8e] {
  margin-top: calc(40vh);
  pointer-events: auto;
  height: 70px;
}

.social-ico[_v-f47d2b8e] {
